.PhotoMode {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: black;
    object-fit: cover;
    overflow: hidden;
    position: fixed;
}

.PhotoMode #bg {
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    user-select: none;
    filter: blur(5px) brightness(80%) grayscale(60%);
}

.PhotoMode .App-header {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    color: white;
}

.PhotoMode video, .PhotoMode #captured {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border: none;
    box-shadow: none;
}

.PhotoMode .countdown {
    position: absolute;
    top: 50%; /* Vertikale Mitte */
    left: 50%; /* Horizontale Mitte */
    transform: translate(-50%, -50%); /* Zentrieren */
    font-size: 10rem; /* Schriftgröße */
    color: rgb(183, 0, 255); /* Countdown-Farbe */
    z-index: 3; /* Sicherstellen, dass es über dem Video angezeigt wird */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8); /* Schatten für bessere Sichtbarkeit */
}

.PhotoMode .button-container {
    position: fixed;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 50px;
    bottom: 0%; 
    z-index: 2;
    opacity : 0.9
}

.PhotoMode button {
    padding: 2vh 3vw;
    font-size: 3vh;
    color: white;
    background-color: black;
    border: 2px solid white;
    border-radius: 4vh;
    cursor: pointer;
}

.PhotoMode button:active {
    transform: scale(0.98);
}

.PhotoMode .start-button {
    background-color: #28a745;
    border: none;
}

.PhotoMode .end-button {
    background-color: #dc3545;
    border: none;
}

.camera-preview{
    width: 100%;
    height: 100%;
    object-fit: contain;
    max-width: 100%;
    max-height: 100%;
}