 body {
    margin: 0;
    padding: 0;
    height: 100%;
    justify-content: center;
    align-items: center;
    color: white;
}

.login-container {
    padding: 20px;
    border-radius: 10px;
    margin-top: 5%;
    margin-left: 30%;
    margin-right: 30%;
}

.login-container h2 {
    margin-bottom: 20px;
}

.login-container label {
    display: block;
    margin: 10px 0 5px;
}

.login-container input {
    width: 100%;
    padding: 10px;
    margin-bottom: 20px;
    border: none;
    border-radius: 5px;
}

#login-button {
    width: 100%;
    padding: 10px;
    background-color: #ff5733;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    
}

#login-button:hover {
    background-color: #e64a19;
}

#back-link {
    display: block;
    margin-top: 20px;
    color: #ff5733;
    text-decoration: none;
}

#back-link:hover {
    color: #e64a19;
}

h2{
    text-align: center;
    font-size: 30px;
}
